<template>
  <div id="bgimg">
    <body id="poster">
      <el-form class="login-container" label-position="left" label-width="0px">
        <h3 class="login_title">Login</h3>
        <el-form-item>
          <el-input
            type="text"
            v-model="loginForm.loginName"
            autocomplete="off"
            placeholder="Username"
            prefix-icon="el-icon-user-solid"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            type="password"
            v-model="loginForm.password"
            autocomplete="off"
            placeholder="Password"
            prefix-icon="el-icon-lock"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-select
            v-model="loginForm.identity"
            placeholder="Please select identity"
            prefix-icon="el-icon-user"
          >
            <el-option label="Ordinary user" value="User"></el-option>
            <el-option label="Administrator" value="Admin"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width: 100%;">
          <el-button
            type="primary"
            style="width: 100%; background: #505458; border: none"
            @click="Login"
            >Login</el-button
          >
        </el-form-item>
        <el-button @click="toRegister">Click me to register</el-button>
      </el-form>
    </body>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        loginName: "",
        password: "",
        identity: "",
      },
    };
  },
  methods: {
    Login() {
      const loginName = this.loginForm.loginName;
      this.axios.post("http://localhost:3231/sys-user/login", this.loginForm).then((resp) => {
        let data = resp.data;
        if (data.success) {
          localStorage.setItem("userRole", loginName === "Admin" ? "Admin" : "User");
          localStorage.setItem("isAuthenticated", true);
          this.loginForm = {};
          this.$message({
            message: `Welcome to home page, ${loginName}!`,
            type: "success",
          });
          if (loginName === "Admin") {
            this.$router.push({ path: "/Admin" });
          } else {
            this.$router.push({ path: "/Home" });
          }
        } else {
          if (data.message === "User not found") {
            this.$message({
              message: "Username does not exist, please register.",
              type: "error",
            });
          } else if (data.message === "Incorrect password") {
            this.$message({
              message: "Incorrect password, please try again.",
              type: "error",
            });
          } else {
            this.$message({
              message: "Login failed, please try again.",
              type: "error",
            });
          }
        }
      });
    },
    toRegister() {
      this.$router.push({ path: "/Register" });
    },
  },
};
</script>

<style>
#poster {
  background-position: center;
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
}
body {
  margin: 0px;
  padding: 0px;
}
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 90px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
</style>
